	module.exports = {"data":{"post":{"html":"<p>This week the schoolkids in Niedersachsen get 2 days off school for *Zeugnisferien*, or report card vacation. As far as I know, the first graders at Christopher's school don't even get report cards, but they get the days off anyway. </p>\n<p>Even better, today his school lets out at 10 am, after exactly 90 minutes in class. So working parents lose 3 days instead of 2. </p>\n<p>That din you'll be hearing in the background is the sound of somewhat more chaos than usual.</p>","fields":{"title":"Report Card Vacation","date":"31 Jan 2007","url":"https://papascott.de/2007-01-31-report-card-vacation/"}}},"pathContext":{"slug":"/2007-01-31-report-card-vacation/","prev":{"fields":{"title":"Airline Crap Shoot 2007","date":"30 Jan 2007","slug":"/2007-01-30-airline-crap-shoot-2007/"}},"next":{"fields":{"title":"Bring Slippers to School","date":"04 Feb 2007","slug":"/2007-02-04-bring-slippers-to-school/"}}}}

